        :title="item.text"
      >
        <div class="menu-item-icon icon" :class="item.icon"></div>
        <div class="menu-item-text" :class="{ 'display': isToggleNavbar }">
          {{ item.text }}
        </div>
      </router-link>
      <!-- <router-link 
            class="menu-item" 
            to="/empmloyee"
            >
              <div class="menu-item-icon"></div>
              <div class="menu-item-text">long ca</div>
          </router-link>
           <router-link 
