<script>
import { mapGetters, mapMutations } from "vuex";
import { GET_TOGGLENAVBAR, SET_TOGGLENAVBAR } from "../../../scripts/constants";
export default {
  name: "Navbar",
  props: {
    navItems: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters({
      isToggleNavbar: GET_TOGGLENAVBAR,
    }),
  },
  methods: {
    ...mapMutations([SET_TOGGLENAVBAR]),
